import React from "react";
import "./User.css";
import { FcHome } from "react-icons/fc";
import { FcCellPhone } from "react-icons/fc";
import { MdEmail } from "react-icons/md";

import { FaGithub, FaLinkedin } from "react-icons/fa";


export function User() {
  return (
    <div className="user">
      <img src="./images/yo.png" alt="" className="user_avatar" />
      <h1 className="user_name">Yoann Anodeau</h1>
      <p className="user_profession">
        Je suis passionné par le développement web, et j'aime bien utiliser
        différents langage{" "}
      </p>
      <div className="user_infos">
        <p className="user_info">
          <FcHome style={{ fontSize: 20 }} /> 3 rue du portail 35590 L'Hermitage
        </p>
        <p className="user_info">
          <FcCellPhone style={{ fontSize: 20 }} />
          <a href="tel:+33648633930">0648633930</a>
        </p>
        <p className="user_info">
          <MdEmail style={{ fontSize: 20 }} />
          <a href="mailto:yo-cbr@hotmail.fr">yo-cbr@hotmail.fr</a>
        </p>
        <p className="user_info">
          <FaLinkedin style={{ fontSize: 20 }} />
          <a href="https://www.linkedin.com/in/yoann-anodeau">https://linkedin.com</a>
        </p>
        <p className="user_info">
          <FaGithub style={{ fontSize: 20 }} />
          <a href="https://github.com/yoannano"> https://github.com</a>
        </p>
      </div>
    </div>
  );
}

export default User;
