import "./App.css";
import { User } from "./components/User";
import Skills from "./components/Skills";
import Profil from "./components/Profil";
import FormationExp from "./components/FormationExp";
import DarkMode from "./components/DarkMode";
import { Preview, print } from "react-html2pdf";
import { MdOutlinePictureAsPdf } from "react-icons/md";


function App() {
  const handleGenerateCv = () => {
    let cvTemplate = document.getElementById("cv-print");
    cvTemplate.setAttribute("style", "width: 210mm !important");
    cvTemplate.classList.add("cv-print");
    document.body.classList.remove("dark");
    setTimeout(() => {
      print("cv", "cv-print");
      cvTemplate.setAttribute("style", "width: 100% !important");
      cvTemplate.classList.remove("cv-print");
    }, 300);
  };
  return (
    <Preview id={"cv-print"}>
      <div className="App">
        <div className="grid_container">
          <div className="sidebar">
            <div className="actions">
              <DarkMode />
              <button onClick={handleGenerateCv}>
                <MdOutlinePictureAsPdf style={{ fontSize: 20 }} />
              </button>
            </div>

            <User />
            <Skills />
          </div>
          <div className="main">
            <Profil />
          
            <FormationExp />
          </div>
        </div>

        <h3> </h3>
      </div>
    </Preview>
  );
}

export default App;
